@import url("https://fonts.googleapis.com/css2?family=Economica&display=swap");

.Layout__container__Pokemon {
  box-sizing: border-box;
  width: 100vw;
  height: auto;
  background-color: #6b6b6b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.List__Pokemon {
  padding: 0px;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  justify-content: center;
}
.List__Pokemon__Card {
  margin: 20px 30px 15px;
  width: 210px;
  height: 307px;
  background: #c4c4c4;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.PokemonCardItem {
  display: flex;
  flex-direction: column;
}

.PokemonCard__stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.PokemonCard__Title {
  margin-top: 0px;
  text-align: center;
  font-family: "Economica", sans-serif;
}

p {
  margin: 10px;
  width: 25px;
  text-align: center;
  font-family: "Economica", sans-serif;
}

.Button_Next_page {
  margin: 10px;
  transform: rotate(90deg);
  width: 30px;
  height: 30px;
  background: url("/mnt/c/Users/YAN/Desktop/PersonalProjects/pokeapi/src/images/arrow.png");
  background-size: cover;
}

.Button_Prev_page {
  margin: 10px;
  transform: rotate(270deg);
  width: 30px;
  height: 30px;
  background: url("/mnt/c/Users/YAN/Desktop/PersonalProjects/pokeapi/src/images/arrow.png");
  background-size: cover;
}

@media (min-width: 1619px) {
  .Layout__container__Pokemon {
    height: 100vh;
  }
}

@media (min-width: 1890px) {
  .List__Pokemon {
    width: 60vw;
  }
}
