@import url("https://fonts.googleapis.com/css2?family=Economica&display=swap");

.Layout__container__aboutme {
  width: 100vw;
  height: 100vh;
  background-color: #6b6b6b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Aboutme__img_primary {
  width: 275px;
  height: 140px;
  background: url("/mnt/c/Users/YAN/Desktop/PersonalProjects/pokeapi/src/images/aboutme-img.png");
  background-size: contain;
  background-repeat: no-repeat;
}

.Layout__btn__aboutme {
  width: auto;
  height: 65px;
  text-align: center;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Aboutme__text_primary {
  padding: 5px;
  line-height: 1.5;
  font-family: "Economica", sans-serif;
  text-align: center;
  font-size: 20px;
}

.Link_social-media {
  font-weight: bold;
  margin: 10px;
  color: #221e1e;
  text-decoration: none;
}
